* {
  font-family: Helvetica, sans-serif, arial;
}

body {
  padding: 60px;
  background-color: rgb(16, 1, 32);
}

h1, h3 {
  color: rgb(244, 238, 248);
}

input {
  color: grey;
  font-size: 1.2rem;
  width: 99%;
  
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  border-radius: 15px;
  background: #e0e0e0;
  box-shadow: 10px 10px 6px #221c1c,-10px -10px 6px #2c2424;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
  border-radius: 10px;
}